$button--size: 15px;
$button--action-size: 50px;

@mixin input-range-track () {
  -webkit-appearance: none;
  cursor: pointer;
  background: $foreground;
  height: 1px;
}

@mixin input-range-thumb ($focused: false) {
  -webkit-appearance: none;
  position: relative;
  z-index: 1;
  cursor: pointer;
  background: $foreground;
  height: $button--size;
  width: $button--size;
  margin-top: -($button--size / 2) + 1px;
  border-radius: 50%;
}

.font-tester {
  margin: $bar--margin-bottom 0;

  [contenteditable] {
    margin: 0 $gutter;
    text-align: center;
    overflow-wrap: break-word;
    overflow: hidden;

    @include mobile {
      margin: 0;
    }

    &, * {
      font-size: Max(8px, calc(var(--font-scale, 1) * 10vmin));
      line-height: var(--line-height, 1);
      letter-spacing: var(--letter-spacing, 0);

      @include mobile {
        font-size: 7vmin;
      }
    }

    p {
      margin-bottom: 0;
    }

    &:focus {
      outline: 0;
    }
  }

  #scale {
    @include reset-button;
    position: fixed;
    left: $gutter;
    top: 50%;
    width: 15vh;
    min-width: 100px;

    transform-origin: 0;
    transform: rotate(90deg) translate(-50%, 50%);
    height: 1px;

    // Increase action area
    cursor: pointer;
    margin-top: $button--action-size / -2;
    margin-left: $button--action-size / 2;
    padding: ($button--action-size / 2) 0;

    @include mobile {
      display: none;
    }

    &::before, &::after {
      content: 'A';
      display: block;
      text-align: center;
      transform-origin: center 0;
      height: 2.2rem;
      line-height: 2.3rem;
      transform: rotate(-90deg);
      font-size: larger;
    }

    &::before {
      font-size: smaller;
      transform: rotate(-90deg) translateY(-100%);
    }

    &:focus {
      outline: none;
      &::-webkit-slider-thumb { @include input-range-thumb(true); }
      &::-moz-range-thumb { @include input-range-thumb(true); }
      &::-ms-thumb  { @include input-range-thumb(true); }
    }

    &::-webkit-slider-runnable-track { @include input-range-track(); }
    &::-webkit-slider-thumb {
      @include input-range-thumb();
      -webkit-appearance: none;
    }

    &::-moz-range-track { @include input-range-track(); }
    &::-moz-range-thumb { @include input-range-thumb(); }
    &::-ms-track { @include input-range-track(); }
    &::-ms-thumb  { @include input-range-thumb(); }
  }

  #invert {
    display: none;
  }

  label[for='invert'] {
    position: fixed;
    display: block;
    right: $gutter;
    top: 50%;
    transform: translate(50%, 50%);

    padding: 0;
    width: $button--size * 1.3;
    height: $button--size * 1.3;

    @include mobile {
      display: none;
    }

    svg {
      width: inherit;
      height: inherit;
    }

    body.is-inverted & {
      filter: invert(100);
    }

    // Increase action area
    &::before {
      content: '';
      position: absolute;
      top: -$button--action-size / 2;
      bottom: -$button--action-size / 2;
      left: -$button--action-size / 2;
      right: -$button--action-size / 2;
    }
  }
}
