.fonts {
  @include no-select;
  margin: calc(#{$gutter} * -1);
  margin-top: calc(#{$bar--margin-bottom} * -1);

  &__item a {
    position: relative;
    display: block;
    border-bottom: 1px solid $accent;

    font-size: 20vmin;
    line-height: 1.3;
    text-align: center;

    h2 {
      transform: translateY(var(--text-optical-align, 0));
      letter-spacing: var(--letter-spacing, 0);
    }

    svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      margin-top: var(--illustration-optical-align, 0);

      display: none;
      fill: $foreground;
      height: 0.75em;
    }

    &:hover {
      color: transparent;
      background: $accent;
    }

    &:hover svg {
      display: block;
    }
  }
}
