.font {
  position: relative;

  &__header {
    position: sticky;
    top: $bar--height;
    padding-top: $bar--margin-bottom;
    margin-top: calc(#{$bar--margin-bottom} * -1);
    background: $background;
  }

  &__header &__nav {
    @include bar(0);
    border-bottom: 1px solid $accent;
    display: grid;

    grid-template-columns: 1fr 1fr 1fr;
    grid-template-areas: 'tabs title links';

    @include mobile {
      grid-template-columns: 1fr 1fr;
      grid-template-rows: $bar--height $bar--height;
      grid-template-areas:
        'title links'
        'tabs tabs';
    }
  }

  &__header &__nav &__title {
    font-size: inherit;
    font-weight: normal;
    text-align: center;
    grid-area: title;

    @include mobile {
      text-align: left;
    }
  }

  &__header &__nav &__links {
    display: flex;

    &--tabs {
      grid-area: tabs;
      @include mobile {
        border-top: 1px solid $accent;
        justify-content: space-between;
      }
    }

    &--links {
      grid-area: links;
      justify-content: flex-end;
    }
  }

  &__header &__nav &__link {
    &:not(:first-child) {
      margin-left: $gutter;
    }
  }

  &__tab-state {
    display: none;
  }

  #fontStyleTab:checked ~ &__header &__nav &__links &__link label[for=fontStyleTab],
  #fontAboutTab:checked ~ &__header &__nav &__links &__link label[for=fontAboutTab] {
    color: $accent;
  }

  #fontStyleTab:not(:checked) ~ &__header &__tabs &__tab--styles,
  #fontAboutTab:not(:checked) ~ &__header &__tabs &__tab--about {
    display: none;
  }

  &__tab {
    margin-top: calc(#{$bar--horizontal-padding} - 4px);
    padding-bottom: $bar--horizontal-padding;

    p:last-child {
      margin-bottom: 0;
    }
  }

  &__tab--styles {
    display: flex;
    flex-wrap: wrap;

    li {
      @include no-select;
      margin-right: $gutter;
    }

    a.is-active {
      border-bottom: 1px solid;
    }
  }

  &__footer {
    @include bar;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background: $accent;
    color: black;
    text-align: center;
  }
}
