.custom {
  position: relative;

  &__header {
    @include bar(0);
    top: $bar--height;
    padding-top: $bar--margin-bottom;
    margin-top: calc(#{$bar--margin-bottom} * -1);
    background: $background;
    border-bottom: 1px solid $accent;
    margin-bottom: $bar--horizontal-padding;
  }

  &__content {
    display: grid;
    gap: $gutter;
    grid-template-columns: cols(3) 1fr;

    @include devices (mobile, tablet) {
      grid-template-columns: 1fr;
    }
  }

  &__content &__text {
    @include desktop {
      position: sticky;
      height: fit-content;
      top: calc(#{$bar--height} + #{$bar--margin-bottom});
    }
  }

  &__content &__text &__metas  {
    margin-top: 1em;
    list-style: disc outside;
    padding-left: 1em;
  }

  &__gallery {
    margin-top: 4px;
  }

  &__gallery figure {
    & + figure {
      margin-top: calc(#{$bar--horizontal-padding} + 4px);
    }

    img {
      max-width: 100%;
      height: auto;
      background: $accent;
    }
  }
}
