.buy {
  display: grid;
  grid-template-columns: [left] cols(3) 1fr [center] cols(4) 1fr [right] cols(3);
  gap: $gutter;

  // This custom media handles a edge case on small desktop screens
  @media only screen and (min-width: 1024px) and (max-width: 1100px) {
    grid-template-columns: [left] cols(3) [center] cols(5) [right] cols(4);
  }

  @include devices (tablet, mobile) {
    display: block;
  }

  @include big-screen {
    grid-template-columns: [left] cols(2) 1fr [center] cols(3.5) 1fr [right] cols(2);
  }

  fieldset:not(:last-of-type) {
    margin-bottom: $bar--height;
  }

  fieldset[data-label]::before {
    @include bar($bar--horizontal-padding);
    content: attr(data-label);
    display: block;
    background: $accent;
  }

  label {
    @include bar($bar--horizontal-padding);
    display: block;
    border-bottom: 1px solid;

    &:hover span {
      border-bottom: 1px solid;
    }
  }

  &__licenses {
    input[type=radio] {
      display: none;
    }

    input:checked + label {
      background: $foreground;
      color: $background;
    }
  }

  &__fonts {
    grid-column-start: center;
  }

  &__font {
    input[type=checkbox] {
      margin-top: 0.45em;
      margin-bottom: -0.45em;
    }

    label[data-base-price]::after {
      content: attr(data-base-price)'€';
      float: right;
    }

    label[data-price]::after {
      content: attr(data-price)'€';
      float: right;
    }

    input[type=checkbox][value=full]:checked ~ label:not([for$=-full])::after {
      content: 'Included';
    }
  }

  &__total {
    position: relative;
    grid-column-start: right;

    &::before {
      background: $foreground !important;
      color: $background !important;
      margin-bottom: calc(#{$bar--height} + 1px);
    }

    input[type=text], textarea {
      @include reset-button;
      @include bar($bar--horizontal-padding);
      width: 100%;
      border-bottom: 1px solid;

      &::placeholder {
        color: $foreground;
        opacity: 0.4;
      }
    }

    textarea {
      padding: calc((#{$bar--height} - 1em) / 2) $bar--horizontal-padding;
      line-height: $line-height;
      margin-bottom: 0;
      vertical-align: bottom;
    }

    #agree {
      margin-top: $bar--height;
    }

    label[for=agree] {
      border: 0;
      padding-bottom: $bar--horizontal-padding;
      line-height: $line-height;
    }
  }

  &__total[data-price]::after {
    @include bar(0);
    content: attr(data-price)'€';
    position: absolute;
    top: 0;
    right: $bar--horizontal-padding;
    color: $background;
  }

  &__licenses, &__total {
    @include desktop {
      top: calc(#{$bar--height} + #{$bar--margin-bottom});
      height: min-content;
      position: sticky;
    }
  }

  button[disabled] {
    pointer-events: none;
    opacity: 0.4;
  }
}
