.menu {
  @include bar;

  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: black;
  color: $accent;

  display: flex;
  justify-content: space-between;

  height: $bar--height;

  svg {
    position: absolute;
    left: 50%;
    top: 0;
    bottom: 0;
    height: 100%;
    transform: translateX(-50%);
    fill: currentColor;
    width: auto;

    @include mobile {
      display: none;
    }
  }

  #home {
    border: 0;
  }

  &__links {
    display: flex;
  }

  &__link:not(:first-child) {
    margin-left: $gutter;
  }

  &__link.is-active a {
    border-bottom: 1px solid;
  }

  &__link[data-text='information' i],
  &__link[data-text='Information'] {
    @include mobile {
      overflow: hidden;
      width: 4ch;
    }
  }
}
