.customs {
  display: grid;
  gap: $gutter;
  grid-template-columns: 1fr 1fr;

  @include devices (tablet, mobile) {
    grid-template-columns: 1fr;
  }

  &__custom h2 {
    @include bar(0);
    border-bottom: 1px solid $accent;
    margin-bottom: $bar--horizontal-padding;

    // Handle multiple-line without touching the parent line-height, used in bar
    // to compute default height
    span {
      display: inline-block;
      line-height: $line-height;
    }
  }

  &__custom svg {
    margin-top: 4px;
    height: auto;
    vertical-align: bottom;
    fill: $foreground;
    background: $accent;
  }

  &__custom a:focus-visible svg,
  &__custom a:hover svg {
    fill: $accent;
    background: $foreground;
  }

  &__custom a:focus-visible h2 {
    color: black;
  }
}
