html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after { box-sizing: inherit; }

body {
  min-height: 100%;
  margin: 0;
  padding: 0;

  font-family: 'Fork', sans-serif;
  font-size: $font-size;
  line-height: $line-height;

  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;

  background: $background;
  color: $foreground;
}

::selection  {
  background: $accent;
  color: $background;
}

img {
  border: 0;
  -ms-interpolation-mode: bicubic;
  vertical-align: bottom;
  width: 100%;
  height: auto;

  &[data-lazyload] { opacity: 0; }
  &[data-lazyload][data-loaded=true][data-fully-loaded] {
    @include easing(120ms, opacity);
    opacity: 1;
  }
}

a {
  -webkit-tap-highlight-color:  rgba(255, 255, 255, 0);
  text-decoration: none;
  color: inherit;
  border-bottom: 1px solid transparent;

  .blocks &,
  p &,
  label &,
  &:hover {
    border-bottom-color: currentColor;
  }

  &:focus-visible {
    outline: 0;
    background: $accent;
    color: $background;
  }
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: normal;
}

p {
  margin-bottom: 1em;
  line-height: $line-height;
}

input[type='checkbox'] {
  @include reset-button;
  cursor: pointer;
  width: 1.4rem;
  height: 1.4rem;
  vertical-align: -2px;
  border: 1px solid $foreground;

  &:focus-visible + label {
    background: $accent;
  }

  &:checked {
    background: $foreground;
  }
}

label {
  @include no-select;
  cursor: pointer;
  line-height: $line-height;

  input[type='checkbox'] + & {
    display: block;
    margin-top: calc(-0.7rem - 1em);
    padding-left: calc(1.4rem + #{$bar--horizontal-padding});
  }
}

button {
  @include reset-button;
  @include bar(0);
  display: block;
  cursor: pointer;
  background: $foreground;
  color: $background;
  padding: 0 1em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%;
  text-align: left;

  &:focus-visible {
    color: $foreground;
    background: $accent;
  }

  &:focus-visible span,
  &:hover span {
    border-bottom: 1px solid;
  }
}

[contentEditable][placeholder]:empty:not(:focus)::before {
  pointer-events: none;
  content: attr(placeholder);
  color: $accent;
  opacity: 0.3;
}

main {
  padding: calc(#{$bar--height} + #{$bar--margin-bottom}) $gutter $gutter;
}

.blocks {
  h1, h2, h3, h4, h5, h6 {
    @include bar(0);
    margin-bottom: $bar--horizontal-padding;
    border-bottom: 1px solid $accent;
    &:not(:first-child) {
      margin-top: $bar--height;
    }
  }

  ul {
    list-style: disc outside;
    padding-left: 1em;
  }

  p {
    overflow-wrap: break-word;
  }
}
