@import 'sass/reset';
@import 'sass/fonts';
@import 'sass/helpers';
@import 'sass/devices';

$debug-grid: false;
$font-size: 1.7rem;
$line-height: 1.45;

:root {
  --foreground: black;
  --background: white;

  --gutter: 4rem;
  --bar--height: 4rem;
  --bar--horizontal-padding: 1.3rem;
  --bar--margin-bottom: 2.8rem;

  @include mobile {
    --gutter: 2rem;
    --bar--height: 4rem;
    --bar--horizontal-padding: 1.3rem;
    --bar--margin-bottom: 1.3rem;
  }
}

body.is-inverted {
  --foreground: white;
  --background: black;
}

$accent: var(--accent, #b89673);
$foreground: var(--foreground, black);
$background: var(--background, white);

$gutter: var(--gutter, 4rem);
$bar--height: var(--bar--height, 4rem);
$bar--horizontal-padding: var(--bar--horizontal-padding, 1.3rem);
$bar--margin-bottom: var(--bar--margin-bottom, 1.3rem);

@mixin bar ($horizontal-padding: $gutter) {
  @include no-select;
  padding: 0 $horizontal-padding;
  line-height: calc(#{$bar--height} + 1px);
}

@function cols ($width) {
  @return calc(#{$width} * (100vw - #{$bar--height}) / 12 - #{$bar--height});
}

@import 'sass/base';
@import 'sass/grid';

@import 'components/Buy';
@import 'components/Custom';
@import 'components/Customs';
@import 'components/Font';
@import 'components/Fonts';
@import 'components/FontTester';
@import 'components/Information';
@import 'components/Menu';
@import 'components/Trial';

.default {
  width: cols(5);
  @include big-screen { width: cols(4); }
  @include tablet { width: cols(7); }
  @include mobile { width: unset; }
}

// Display the menu logo as fullscreen background during loading
html.js body.is-loading {
  overflow: hidden;

  &::before {
    content: '';
    z-index: 3;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: $background;
  }

  .menu {
    z-index: 4;
    pointer-events: none;
    background: transparent;

    a { display: none; }

    #logo { display: block; }
    #logo svg {
      display: block !important;
      position: fixed;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      height: 20vmin;
      fill: $accent;
    }
  }
}
